.search-box__flex {
    justify-content: flex-start;
    margin-left: rem(10px);
}

.search-box {
    padding-left: rem(80px);
    padding-right: 0;
    background-position: 86% 28%,
        96% 19%,
        89% 8%,
        93% 45%;
}

.search-box__form {
    display: flex;
    align-items: baseline;
    justify-content: center;
    position: relative;
}

.search-box__label {
    flex-grow: 100%;
    width: 770px;
}

.btn-search {
    position: absolute;
    transform: translateX(299px);

    &:hover {
        color: $red;
        background-color: $white;
        outline: 1px solid $red;
    }
}

.search-box__svg {
    margin-bottom: rem(15px);
    transform: scale(1.24);
}

.search-box__svg_building:hover .tooltip {
    visibility: visible;
}

.tooltip1 {
    top: rem(55px);
    left: rem(-85px);
}

.search-box__svg_car:hover .tooltip {
    visibility: visible;
}

.tooltip2 {
    top: rem(55px);
    left: rem(-35px);
}

.search-box__svg_bag:hover .tooltip {
    visibility: visible;
}

.tooltip3 {
    top: rem(55px);
    left: rem(15px);
}

.search-box__svg_general .tooltip::after {
    content: "";
    position: absolute;
    bottom: 96%;
    left: 50%;
    margin-left: rem(-8px);
    border-width: rem(8px);
    border-style: solid;
    border-color: transparent transparent $tipColor transparent;
}