.search-box {
    align-items: flex-start;
    background-size: 51px 52px,
        39px 62px,
        52px 50px,
        69px 65px;
    background-position: 89% 22%,
        100% 16%,
        93% 5%,
        98% 36%;
}

.search-box__title {
    font-size: rem(60px);
    line-height: rem(82px);
    margin-bottom: rem(-10px);
}

.search-box__subtitle {
    font-size: rem(18px);
    line-height: rem(17px);
}

.search-box__form {
    padding-left: rem(15px);
}

input::placeholder {
    color: $blackHolder;
    font-size: rem(16px);
    line-height: rem(15px);
    font-family: $baseFontFamily;
}

.search-box__label {
    padding: 20px 0;
    width: 670px;
    box-shadow: 4px 4px 3px 2px $white;

    &::before {
        top: rem(16px);
        left: rem(22px);
        transform: scale(1);
    }
}

.search-box__input {
    width: 80%;
}

.btn-search {
    margin-top: rem(10px);
}

.search-box__svg {
    order: 0;
    align-self: center;
    margin-top: rem(-60px);
    flex-basis: 25%;
    text-align: center;
}

.search-box__flex {
    margin-top: rem(30px);
    flex-direction: row;
    align-items: center;
    width: 100%;
    justify-content: center;
}

.search-box__content {
    margin-top: 0;

    &_sec {
        order: 2;
    }
}