.service-bg::after {
    border-radius: 100%;
}

.service-box {
    align-items: center;
}

.service-box__title {
    font-size: rem(48px);
    line-height: rem(65px);
    padding-left: 0;
    padding-right: 0px;
    margin-bottom: rem(20px);
}

.service-box__flex {
    align-items: flex-start;
}

.service-box__item {
    flex-direction: row;
    align-items: flex-end;
    padding-left: rem(80px);
    margin-top: 0;
}

.service-box__subtitle {
    padding-left: rem(50px);
    margin-bottom: rem(10px);
    font-size: rem(24px);
    line-height: rem(33px);
}

.service-box__text {
    text-align: left;
    font-size: rem(16px);
    line-height: rem(15px);
    padding-right: rem(100px);
}

.service-box_img1 {
    width: 66px;
    height: 86px;
}

.service-box_img2 {
    width: 68px;
    height: 87px;
}

.service-box_img3 {
    width: 67px;
    height: 88px;
}