.popular-box {
    @include before(215px, 225px) {
        background: url(#{$iPath}/dots.png) no-repeat;
        top: 13%;
        left: 72%;
    }
}

.popular-bg::after {
    border-radius: 100%;
}

.card-box {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.card-box__item {
    justify-content: flex-end;
    margin-right: rem(115px);

    &:nth-child(2n) {
        margin-right: 0px;
    }
}