.water-mark {
    margin-top: rem(100px);
    margin-bottom: rem(-20px);
}

.main-box__title {
    font-size: rem(60px);
    line-height: rem(82px);
}

.main-box-text {
    font-size: rem(16px);
    padding: 0px 100px;
    margin-top: rem(-30px);
    margin-bottom: rem(50px);
}

.process-box {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.process-box__item {
    width: 48%;

    &::before {
        transform: none;
    }
}

.process-box__item:nth-child(1)::before {
    top: rem(140px);
}

.process-box__item:nth-child(2)::before {
    transform: matrix(-0.46, 0.89, 0.89, 0.46, 0, 0);
    top: rem(210px);
    left: rem(-20px);
}

.process-box__title_first::before {
    transform: scale(1.1);
    top: rem(-90px);
}

.process-box__title_second::before {
    transform: scale(1.1);
    top: rem(-90px);
}

.process-box__title_third::before {
    transform: scale(1.1);
    top: rem(-90px);
}