.popular-box {
    display: flex;
    flex-direction: column;
    margin-top: rem(60px);
    align-items: center;
}

.popular-bg {
    background-color: $lightYellow;
    width: 100%;
    position: relative;

    @include after(100%, 200px) {
        background-color: $lightYellow;
        top: -30px;
        left: 0;
        border-radius: 30%;
        z-index: -1;
    }
}

.card-box {
    display: flex;
    flex-direction: column;
    margin-top: rem(20px);
}

.card-box__item {
    background-color: $white;
    margin-bottom: rem(20px);
    box-shadow: 0px 0px 25px $cardBoxColor;
    border-radius: rem(10px);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 10px 20px 10px;
    width: 250px;
    height: 290px;
}

.card-box__title {
    margin-left: 0;
}

.card-box__text {
    margin-top: rem(-20px);
}

.card-box__img {
    margin-bottom: rem(20px);
}