.main-box {
    margin-top: rem(100px);
}

.main-box-text {
    padding: 0 300px;
}

.process-box {
    flex-wrap: nowrap;
    justify-content: center;
    max-width: 1148px;

    &__item {
        width: 30%;
        margin-right: rem(10px);
    }
}

.process-box__item:last-of-type {
    margin-bottom: rem(120px);
    margin-right: 0;
}

.process-box__item:nth-child(1)::before {
    top: rem(180px);
}

.process-box__item:nth-child(2)::before {
    transform: none;
    top: rem(180px);
    left: rem(200px);
}