.water-mark_blue {
    letter-spacing: 0;

    &::before,
    &::after {
        background: $lightBlue;
    }
}

.user-box {
    width: rem(515px);
    display: flex;
    padding: 30px 20px 30px 0;
    box-shadow: 0px 0px 50px $userShadowBlack;

    &__circle {
        position: absolute;
        top: rem(20px);
        left: rem(-30px);
    }

    &__content {
        margin-left: rem(100px);
    }
}

.feedback-box__stars {
    bottom: rem(78px);
    left: rem(270px);
}