* {
  box-sizing: border-box;
}

html {
  @include getCssVar(font-size);
}

body {
  margin: 0;
  padding: 0;
  @include getCssVar(font-family);
  font-size: 1rem;
  min-height: 100vh;
}

h1,
h2,
h3 {
  font-family: $headingFontFamily;
}

.wrap {
  @include before(100%, 500px) {
    left: 0;
    top: 0;
    background-color: $headBgColor;
    z-index: -1;
  }

  @include after(100%, 300px) {
    clip-path: ellipse(115% 100% at 50% 0%);
    top: 330px;
    background-color: $headBgColor;
    z-index: -1;
  }
}

.container {
  max-width: 1700px;
  width: 100%;
  margin: auto;
  padding: 15px 15px;
}