.nav-box {
    display: flex;
    list-style: none;
    align-items: baseline;
}

.nav-box__item {
    margin-right: rem(10px);
    font-size: rem(12px);
    line-height: rem(15px);

    &:last-of-type {
        margin-right: 0;
    }
}

.nav-box__link {
    text-decoration: none;

    &_und {
        text-decoration: $black underline;
        margin-top: rem(10px);
    }
}

.nav-box__btn {
    color: $white;
    font-size: rem(14px);
    line-height: rem(22px);
    background-color: $blue;
    border-radius: rem(20px);
    cursor: pointer;
    padding: 6px 20px;

    &:hover {
        background-color: $white;
        color: $blue;
        outline: 1px solid $blue;
    }
}

.svg-icon_burger {
    display: none;
}

.svg-icon_arrow {
    width: 16px;
    height: 18px;
    margin-bottom: rem(-5px);
    margin-left: rem(5px);
    transition: transform 0.3s ease-in-out;

    &:hover {
        transform: rotate(180deg);
    }
}

.svg-icon_redarrow {
    width: 18px;
    height: 21px;
    margin-bottom: rem(-5px);
    transition: transform 0.2s ease-in;

    &:hover {
        transform: scale(1.2);
    }
}

.box-image_flag {
    width: 100%;
    max-width: 100%;
}