.app-bg {
    margin-top: 0;
}

.app-box__title {
    padding-top: rem(40px);
}

.app-box {
    padding-left: rem(30px);
}

.app-box__text {
    padding-right: rem(80px);
    font-size: rem(24px);
    line-height: rem(38px);
}

.app-box__item {
    display: flex;
    flex-direction: column;
    margin-top: rem(50px);
}

.app-box__store {
    width: 220px;
    height: 65px;
    background-color: $black;
    border: 1px solid $greyBorder;
    border-radius: rem(10px);
    margin-bottom: rem(30px);
}

.app-box__content {
    margin-left: rem(70px);
    font-size: rem(12px);
    letter-spacing: rem(1px);
    margin-bottom: rem(-15px);
}

.app-box__link {
    margin-left: rem(70px);
    text-decoration: none;
    margin-top: rem(-10px);
    font-size: rem(26px);
    color: $white;
    letter-spacing: rem(-1px);
}

.app-box__store_apple {
    position: relative;

    @include before(100%, 100%) {
        background: url(#{$iPath}/icons/apple.svg) no-repeat;
        transform: scale(1.5);
        top: 22px;
        left: 70px;
    }
}

.app-box__store_google {
    position: relative;

    @include before(100%, 100%) {
        background: url(#{$iPath}/icons/playMarket.svg) no-repeat;
        transform: scale(1.5);
        top: 25px;
        left: 75px;
    }
}

.app-box__hidden {
    display: none;
}

.app-box__wrap {
    position: relative;
    top: 0;
    right: 0;
    margin-bottom: rem(-8px);
}

.app-box__img {
    width: 100%;
    margin-left: rem(136px);
    margin-top: rem(-273px);
}