.tool-box {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.tool-box__item {
    flex-basis: rem(360px);
    margin-right: rem(18px);

    &:nth-child(2n) {
        margin-right: 0;
    }
}

.tool-box__item-wrap {
    margin-left: rem(25px);
}

.tool-box__border {
    margin-left: rem(25px);
}