.footer-bg {
    background-color: $headBgColor;
    width: 100%;
    height: auto;
    position: relative;
    top: rem(60px);
}

.footer-box {
    padding-top: rem(20px);

    &__links {
        display: none;
    }
}

.slogan-box {
    background: url(#{$iPath}/icons/logo.svg) no-repeat;
    background-position: 0 4%;
    margin-bottom: rem(40px);
    margin-left: rem(30px);
}

.slogan-box__title {
    font-weight: 800;
    font-size: rem(36px);
    line-height: rem(49px);
    margin-left: rem(65px);
}

.slogan-box__subtitle {
    font-size: rem(14px);
    line-height: rem(13px);
    margin: -35px 0 30px 68px;
}

.slogan-box__content {
    font-size: rem(16px);
    line-height: rem(26px);
}

.slogan-box__content_bold {
    font-weight: 700;
}

.community-box {
    margin-left: rem(30px);

    &__title {
        font-weight: 600;
        font-size: rem(24px);
        line-height: rem(30px);
    }

    &__text {
        font-size: rem(16px);
        line-height: rem(34px);
    }
}

.app-box__hidden {
    margin: 50px 0;
}

.footer-icons {
    text-align: center;
    margin-bottom: rem(40px);
}

.icons-wrapper {
    width: rem(44px);
    height: rem(44px);
}

.copyright {
    font-size: rem(14px);
    line-height: rem(24px);
    text-align: center;
    border-top: 1px solid $semiWhite;
    padding: 10px 30px 10px 30px;
}