.service-bg {
    margin-top: rem(80px);
    background-color: $aqua;
    position: relative;
    z-index: -1;

    @include after(100%, 200px) {
        background-color: $aqua;
        top: -30px;
        left: 0;
        border-radius: 30%;
        z-index: -1;
    }
}

.service-box {
    display: flex;
    flex-direction: column;
    margin-top: rem(60px);
}

.service-box__title {
    font-size: rem(32px);
    line-height: rem(44px);
    text-align: left;
    padding-right: rem(30px);
    padding-left: rem(10px);
    max-width: rem(600px);
}

.service-box__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 30px 0;
}

.service-box_img1 {
    background: url(#{$iPath}/icons/interaction.svg) no-repeat;
    width: rem(50px);
    height: rem(52px);
    display: block;
    position: relative;

    @include before(89px, 89px) {
        background: $pink;
        border-radius: 22px;
        z-index: -1;
        top: -16px;
        right: -17px;
    }
}

.service-box_img2 {
    background: url(#{$iPath}/icons/joystick.svg) no-repeat;
    width: rem(52px);
    height: rem(52px);
    display: block;
    position: relative;

    @include before(89px, 89px) {
        background: $orange;
        border-radius: 22px;
        z-index: -1;
        top: -16px;
        right: -17px;
    }
}

.service-box_img3 {
    background: url(#{$iPath}/icons/calculator.svg) no-repeat;
    width: rem(50px);
    height: rem(51px);
    display: block;
    position: relative;

    @include before(89px, 89px) {
        background: $purple;
        border-radius: 22px;
        z-index: -1;
        top: -16px;
        right: -17px;
    }
}

.service-box__flex {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: rem(600px);
}

.service-box__subtitle {
    font-weight: 700;
    font-size: rem(20px);
    line-height: rem(27px);
    margin-top: rem(40px);
    margin-bottom: rem(-10px);
}

.service-box__text {
    font-size: rem(14px);
    line-height: rem(17px);
    text-align: center;
    padding: 0 50px;
}