.feedback-box {
    display: flex;
    flex-direction: column;
    margin-top: rem(30px);

    &__flex {
        display: flex;
        flex-direction: column;
        margin-top: rem(20px);
    }

    &__stars {
        position: absolute;
        bottom: rem(47px);
        left: rem(160px);
    }
}

.water-mark_blue {
    letter-spacing: rem(-1px);
}

.user-box {
    width: rem(300px);
    border: 1px solid $borderColor;
    border-radius: rem(10px);
    margin: auto;
    margin-bottom: rem(20px);
    position: relative;
}

.user-box__circle {
    background-color: $grey;
    width: rem(100px);
    height: rem(100px);
    border-radius: 100%;
    margin: 20px auto;
}

.user-box__content {
    margin-left: rem(10px);
}

.user-box__text {
    font-size: rem(14px);
    line-height: rem(26px);
}

.user-box__person {
    font-weight: 700;
    font-size: rem(18px);
    line-height: rem(22px);
}

.user-box__type {
    font-size: rem(14px);
    line-height: rem(13px);
}