.search-box {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: rem(30px);
    background: url(#{$iPath}/icons/dron.svg) no-repeat,
    url(#{$iPath}/icons/clock.svg) no-repeat,
    url(#{$iPath}/icons/photo.svg) no-repeat,
    url(#{$iPath}/icons/laptop.svg) no-repeat;
    background-size: 25px 32px,
        20px 38px,
        26px 30px,
        34px 40px;
    background-position: 86% 42%,
        101% 39%,
        92% 35%,
        99% 47%;

    &__title {
        font-weight: 700;
        font-size: rem(36px);
        line-height: rem(49px);
    }

    &__subtitle {
        font-size: rem(16px);
        line-height: rem(19px);
        padding: 0 10px;
    }
}

input::placeholder {
    color: transparent;
}

.search-box__form {
    text-align: center;
}

.search-box__label {
    display: block;
    border: 1px solid $searchBoxColor;
    background-color: $white;
    border-radius: rem(33px);
    padding: 5px 0;
    width: rem(270px);
    box-shadow: 2px 2px 2px 2px $white;
    margin: 10px 0;
    position: relative;
    cursor: pointer;

    @include before(100%, 100%) {
        background: url(#{$iPath}/icons/search.svg) no-repeat;
        top: 2px;
        left: -20px;
        transform: scale(0.75);
    }
}

.btn-search {
    border-radius: rem(28px);
    background-color: $red;
    color: $white;
    padding: 18px 51px;
    font-weight: 700;
    font-size: rem(16px);
    line-height: rem(20px);
    text-transform: uppercase;
    cursor: pointer;
}

.search-box__flex {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.search-box__svg {
    order: -1;
    margin-top: rem(30px);
}

.search-box__svg_building {
    position: relative;
    cursor: pointer;

    @include before(50px, 50px) {
        background: url(#{$iPath}/icons/building.svg) no-repeat;
        top: 0;
        left: -70px;
        transform: scale(0.76);
    }
}

.search-box__svg_car {
    position: relative;
    cursor: pointer;

    @include before(50px, 50px) {
        background: url(#{$iPath}/icons/car.svg) no-repeat;
        top: 0;
        left: -20px;
        transform: scale(0.76);
    }
}

.search-box__svg_bag {
    position: relative;
    cursor: pointer;

    @include before(50px, 50px) {
        background: url(#{$iPath}/icons/bag.svg) no-repeat;
        top: 0px;
        left: 30px;
        transform: scale(0.76);
    }
}

.tooltip {
    visibility: hidden;
    width: rem(80px);
    background-color: $tipColor;
    color: $white;
    font-family: $baseFontFamily;
    font-weight: 400;
    text-align: center;
    font-size: rem(11px);
    border-radius: rem(30px);
    padding: 8px 0;
    position: absolute;
    z-index: 1;
}

.search-box__content {
    margin-top: rem(100px);
    font-weight: 700;
    font-size: rem(16px);

    &_sec {
        margin-top: rem(-10px);
    }
}

.search-box__link {
    color: $red;
    font-weight: 700;
}