.app-bg {
    width: 100%;
    background: url(#{$iPath}bgPurple.png);
    margin-top: rem(30px);
    padding-bottom: rem(5px);
    height: 100%;
    margin-bottom: 30px;
    overflow: hidden;
}

.app-box {
    margin-left: rem(10px);
    color: $white;

    &__title {
        font-size: rem(36px);
        line-height: rem(49px);
        text-align: left;
    }

    &__text {
        font-size: rem(20px);
        line-height: rem(32px);
        text-align: left;
        margin-left: initial;
        padding-right: rem(30px);
    }
}

.app-box__item {
    display: none;
}

.app-box__hidden {
    display: flex;
    justify-content: center;

    &_general {
        width: rem(42px);
        height: rem(42px);
        background-color: $black;
        border: 1px solid $greyBorder;
        border-radius: rem(5px);
        margin-right: rem(40px);
        position: relative;
    }
}

.app-box__hidden_apple {
    margin-right: 0;

    @include before(24px, 30px) {
        background: url(#{$iPath}/icons/apple.svg) no-repeat;
        top: rem(3px);
        left: rem(8px);
    }
}

.app-box__hidden_google {
    @include before(25px, 28px) {
        background: url(#{$iPath}/icons/playMarket.svg) no-repeat;
        top: rem(5px);
        left: rem(8px);
    }
}

.app-box__img {
    width: 115%;
    height: auto;
    margin-left: rem(-20px);
    max-width: rem(757px);
    max-height: rem(509px);
}