.partner-box {
    margin: auto;
    margin-top: rem(80px);
}

.partner-box__text {
    font-weight: 700;
    font-size: rem(24px);
    line-height: rem(33px);
    font-family: $headingFontFamily;
    text-align: center;
    margin-bottom: rem(40px);
}

.partner-box__flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.partner-box__item {
    width: 138px;
    height: 103px;
    border: 1px solid $borderColor;
    margin-right: rem(5px);
    border-radius: rem(5px);
    margin-bottom: rem(20px);
}

.partner-box__item_first {
    background: url(#{$iPath}logo5.png) no-repeat;
    background-position: 50% 50%;
}

.partner-box__item_second {
    background: url(#{$iPath}logo4.png) no-repeat;
    background-position: 50% 50%;
}

.partner-box__item_third {
    background: url(#{$iPath}logo3.png) no-repeat;
    background-position: 50% 50%;
}

.partner-box__item_fourth {
    background: url(#{$iPath}logo2.png) no-repeat;
    background-position: 50% 50%;
}

.partner-box__item_fifth {
    background: url(#{$iPath}logo1.png) no-repeat;
    background-position: 50% 50%;
}