.main-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: rem(35px);
}

.water-mark {
    display: flex;
    justify-content: center;
    align-items: center;

    &::before {
        @include mark() {
            margin-right: rem(8px);
        }
    }

    &::after {
        @include mark() {
            margin-left: rem(8px);
        }
    }
}

.main-box__title {
    font-size: rem(36px);
    line-height: rem(49px);
    text-align: center;
}

.main-box-text {
    margin-top: rem(-10px);
    font-size: rem(14px);
    line-height: rem(26px);
    text-align: center;
    padding: 0 20px;
}

.process-box {
    margin-top: rem(70px);
}

.process-box__item {
    margin-bottom: rem(120px);
    position: relative;

    &:last-of-type {
        margin-bottom: 0;
    }

    @include before(126px, 26px) {
        background: url(#{$iPath}/icons/arrowBig.svg) no-repeat;
        bottom: -70px;
        right: 0;
        transform: matrix(-0.09, 1, 1, 0.09, 0, 0);
    }

    &:last-of-type::before {
        visibility: hidden;
    }
}

.process-box__item_first {
    @include after(78px, 78px) {
        background: $lightPink;
        border-radius: 22px;
        top: -30px;
        left: 20px;
        z-index: -1;
    }
}

.process-box__item_second {
    @include after(78px, 78px) {
        background: $lightGreen;
        border-radius: 22px;
        top: -30px;
        left: 20px;
        z-index: -1;
    }
}

.process-box__item_third {
    @include after(78px, 78px) {
        background: $lightCyan;
        border-radius: 22px;
        top: -30px;
        left: 20px;
        z-index: -1;
    }
}

.process-box__title {
    font-weight: 600;
    font-size: rem(20px);
    line-height: rem(27px);
    margin-left: rem(50px);
    position: relative;
}

.process-box__title_first {
    @include before(77px, 58px) {
        background: url(#{$iPath}/icons/bigSearch.svg) no-repeat;
        top: -75px;
        left: 0;
    }
}

.process-box__title_second {
    @include before(54px, 57px) {
        background: url(#{$iPath}/icons/receipt.svg) no-repeat;
        top: -75px;
        left: 0;
    }
}

.process-box__title_third {
    @include before(65px, 56px) {
        background: url(#{$iPath}/icons/people.svg) no-repeat;
        top: -75px;
        left: 0;
    }
}

.process-box__text {
    font-size: rem(14px);
    line-height: rem(26px);
    padding: 0 50px;
}