.footer-box {
    margin-top: rem(40px);
}

.other-links {
    display: initial;
}

.footer-links {
    text-decoration: none;
    color: $blackHolder;
    font-size: rem(16px);
    line-height: rem(34px);
}

.footer-box__links_title {
    font-weight: 600;
    font-size: rem(24px);
    line-height: rem(30px);
}

.footer-box__flex {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-box__flex1 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: baseline;
}

.slogan-box {
    width: rem(350px);
    margin-left: 0;
    background-position: 0% 15%;
}

.community-box {
    width: rem(550px);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    margin-left: rem(70px);
}

.community-box__item {
    order: 5;
    display: flex;
    flex-direction: row;
    margin-top: rem(-10px);
}

.community-box__text {
    order: 3;
}

.footer-icons {
    order: 4;
}

.community-box__store {
    width: rem(154px);
    height: rem(46px);
    margin-right: rem(10px);

    &::before {
        transform: scale(1);
        top: rem(7px);
        left: rem(13px);
    }
}

.community-box__content {
    color: $white;
    font-size: rem(10px);
    margin-left: rem(45px);
    margin-top: rem(5px);
}

.community-box__link {
    font-size: rem(18px);
    margin-left: rem(45px);
    margin-top: 0;
}