.svg-box {
    display: flex;
}

.svg-icon {
    width: rem(29px);
    height: rem(29px);
    margin-top: rem(23px);
    margin-right: rem(5px);
}

.header-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: rem(15px);
    margin-right: rem(15px);

    &__title {
        font-weight: 800;
        font-size: rem(22px);
        line-height: rem(30px);
    }

    &__text {
        font-size: rem(10px);
        line-height: rem(12px);
        transform: translate(-74px, 35px);
    }
}