.featured-box {
    margin-top: rem(80px);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.tool-box {
    max-width: rem(1500px);
    display: flex;
    flex-direction: column;
    margin-top: rem(50px);
    align-items: center;
    margin-bottom: rem(30px);
}

.tool-box__item {
    display: flex;
    flex-direction: column;
    width: rem(300px);
    background-color: $lightGrey;
    margin-bottom: rem(20px);
    border-radius: rem(10px);
}

.tool-box__bg {
    background-color: $grey;
    width: 100%;
    height: rem(250px);
    border-radius: rem(10px);
}

.tool-box__title {
    font-weight: 600;
    font-size: rem(24px);
    line-height: rem(33px);
}

.tool-box__subtitle {
    font-size: rem(14px);
    line-height: rem(13px);
    margin-top: rem(-10px);
    margin-bottom: rem(30px);
}

.tool-box__link {
    font-size: rem(14px);
    text-decoration: none;
}

.tool-box__content {
    font-size: rem(18px);
    line-height: rem(17px);
}

.tool-box__price {
    text-decoration: none;
    color: $red;
    font-weight: 800;
}

.tool-box__person {
    font-weight: 700;
    font-size: rem(16px);
    line-height: rem(20px);
    margin-top: rem(20px);
}

.tool-box__review {
    font-size: rem(12px);
    line-height: rem(11px);
    color: $semiGrey;
}

.tool-box__location {
    font-size: rem(14px);
    line-height: rem(13px);
}

.tool-box__border {
    align-self: center;
    width: 90%;
    margin-top: rem(20px);
    border-top: 1px solid $semiWhite;
    margin-bottom: rem(20px);
}

.tool-box__item-wrap {
    margin-left: rem(10px);
}

.tool-box__flex {
    display: flex;
    align-items: center;
}

.tool-box__stars {
    background: url(#{$iPath}/icons/star.svg) no-repeat;
    width: rem(17px);
    height: rem(16px);
    display: block;
}

.tool-box__siluet {
    background: url(#{$iPath}/icons/siluet.svg) no-repeat;
    width: rem(16px);
    height: rem(16px);
    display: inline-block;
    margin-right: rem(5px);
    vertical-align: baseline;
}

.tool-box__ancor {
    background: url(#{$iPath}/icons/ancor.svg) no-repeat;
    width: rem(12px);
    height: rem(16px);
    display: inline-block;
    margin-right: rem(5px);
    vertical-align: top;
}

.tool-box__btn {
    align-self: center;
}